ing-left: 4%;\r\n}\r\n\r\n.header-active {\r\n\tbox-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);\r\n\tbackground-color: var(--white);\r\n\tposition: fixed;\r\n\ttop: 0;\r\n\tz-index: 2;\r\n\ttransition: all 0.5s ease;\r\n\tpadding-top: 15px;\r\n\tpadding-bottom: 15px;\r\n}\r\n\r\n.navbar {\r\n\tdisplay: flex;\r\n\tjustify-content: space-between;\r\n\talign-items: center;\r\n\twidth: 100%;\r\n\tz-index: 2;\r\n}\r\n\r\n.logo-desktop {\r\n\tdisplay: flex;\r\n\tjustify-content: space-between;\r\n\talign-items: center;\r\n\tgap: 10px;\r\n}\r\n\r\n.logo-desktop h1 {\r\n\tfont-size: 0.7rem;\r\n\tcolor: var(--orange);\r\n}\r\n\r\n.logo-wrapper img {\r\n\tmix-blend-mode: multiply;\r\n\twidth: 50px;\r\n}\r\n\r\n.mobile-menu {\r\n\tposition: fixed;\r\n\ttop: 0;\r\n\tright: 0;\r\n\tbottom: 0;\r\n\twidth: 0;\r\n\tbackground: var(--deep-brown);\r\n\tbackdrop-filter: blur(8px);\r\n\tdisplay: block;\r\n\ttransition: all 1s ease;\r\n\tz-index: 2;\r\n}\r\n\r\n.active {\r\n\twidth: 100%;\r\n}\r\n\r\n.bars {\r\n\tcolor: var(--deep-brown);\r\n\tfont-size: 25px;\r\n\tcursor: pointer;\r\n}\r\n\r\n.times {\r\n\tcolor: var(--orange);\r\n\tfont-size: 25px;\r\n\tmargin-top: 40px;\r\n\tmargin-left: 40px;\r\n\tcursor: pointer;\r\n}\r\n\r\n.nav-list {\r\n\tmargin-top: 70px;\r\n\tmargin-left: 28px;\r\n\tmargin-right: 28px;\r\n}\r\n\r\n.nav-list li {\r\n\tmargin-bottom: 20px;\r\n\tpadding: 5px 0;\r\n\ttext-align: center;\r\n\tlist-style-type: none;\r\n}\r\n\r\n.nav-list li a,\r\n.link {\r\n\tfont-weight: bolder;\r\n\tcolor: var(--white);\r\n\tcursor: pointer;\r\n}\r\n\r\n.nav-list li:hover,\r\n.link:hover {\r\n\tborder-radius: 10px;\r\n\tborder-bottom: 3px solid var(--orange);\r\n}\r\n\r\n.nav-list li:hover a {\r\n\tcolor: var(--white);\r\n}\r\n\r\n.intro {\r\n\tdisplay: flex;\r\n\tjustify-content: center;\r\n\talign-items: center;\r\n\tflex-direction: column;\r\n\ttext-align: center;\r\n\tmargin-top: 150px;\r\n}\r\n\r\n.intro-title img {\r\n\twidth: 80%;\r\n}\r\n\r\n.intro p {\r\n\tcolor: var(--white)